import S from 'fluent-json-schema'
import { VAULT_TYPES_ROUTES_KEY } from './vault-type.const'
import { sClientEntity, sLocales, sPageSearchQuery } from '../core'
import { ClientEntity, Locales, Page, PageSearchQuery } from '../core'

/**
 * Client Vault interface
 */
export interface VaultType extends VaultTypeData, ClientEntity {}

export function sVaultType(isPartial?: boolean) {
  return S.object()
    .id('#vaultType')
    .additionalProperties(false)
    .extend(sClientEntity(isPartial))
    .extend(sVaultTypeData(isPartial))
}

export interface VaultTypeData {
  code: string
  name: Locales<string>
  description?: Locales<string>
}

export function sVaultTypeData(isPartial?: boolean) {
  return S.object()
    .additionalProperties(false)
    .prop('code', S.string())
    .prop('name', sLocales())
    .prop('description', sLocales())
    .required(isPartial ? [] : ['code', 'name'])
}

export enum VaultTypeErrorCodes {
  collision = 'VAULT_TYPE_COLLISION',
  notFound = 'VAULT_TYPE_NOT_FOUND',
  notDeletable = 'VAULT_TYPE_NOT_DELETABLE',
  alreadyExists = 'VAULT_TYPE_ALREADY_EXISTS',
}

export type VaultTypeFields =
  | '_id'
  | 'code'
  | 'name'
  | 'description'
  | 'createdAt'
  | 'createdBy'
  | 'updatedAt'
  | 'updatedBy'

export const VAULT_TYPE_FIELDS = [
  '_id',
  'code',
  'name',
  'description',
  'createdAt',
  'createdBy',
  'updatedAt',
  'updatedBy',
]

export const VAULT_TYPE_SORT_FIELDS = ['code']

export interface VaultTypesSearchQuery
  extends PageSearchQuery<'code', VaultTypeFields> {
  code?: string
  'code:ct'?: string
}

// Route schema
export function sVaultTypesSearchSchema() {
  return S.object()
    .additionalProperties(false)
    .description('List of vault type IDs.')
    .prop('code', S.string())
    .description('Code of the vault type that must match.')
    .prop('code:ct', S.string())
    .description('Code of the vault type that must be contained.')
    .extend(sPageSearchQuery(VAULT_TYPE_FIELDS, VAULT_TYPE_SORT_FIELDS))
}

export interface VaultTypesClientModel {
  search: (params?: VaultTypesSearchQuery) => Promise<Page<VaultType>>
  list: (params?: VaultTypesSearchQuery) => Promise<VaultType[]>
  findOne: (params?: VaultTypesSearchQuery) => Promise<VaultType | undefined>
  create: (body: VaultTypeData) => Promise<VaultType>
}

export enum VaultTypeRoutes {
  v1Create = `v1/${VAULT_TYPES_ROUTES_KEY}`,
  v1Delete = `v1/${VAULT_TYPES_ROUTES_KEY}/:typeId`,
  v1Read = `v1/${VAULT_TYPES_ROUTES_KEY}/:typeId`,
  v1Update = `v1/${VAULT_TYPES_ROUTES_KEY}/:typeId`,
  v1Search = `v1/${VAULT_TYPES_ROUTES_KEY}`,
}
